<template>
  <a-form @submit="handleSubmit" :autoFormCreate="(form)=>{this.form = form}" class="login-container">
    <h2 class="title"></h2>
    <a-form-item label='' fieldDecoratorId="name" :fieldDecoratorOptions="{rules: [{ required: true, message: '' }]}">
      <a-input />
    </a-form-item>
    <a-form-item label='' fieldDecoratorId="phone" :fieldDecoratorOptions="{rules: [{ required: true, message: '' }]}">
      <a-input />
    </a-form-item>
    <a-form-item label='' fieldDecoratorId="email" :fieldDecoratorOptions="{rules: [{ required: true, message: '' }]}">
      <a-input />
    </a-form-item>
    <a-form-item label=''  fieldDecoratorId="userType"
      :fieldDecoratorOptions="{rules: [
           {required: true, message: '' }
        ]
        }">
      <a-select defaultValue='1'>
        <a-select-option value='1'></a-select-option>
      </a-select>
    </a-form-item>
    <a-form-item label=''  fieldDecoratorId="address"
      :fieldDecoratorOptions="{rules: [
           {required: true, message: '' }]
        }">
      <a-cascader :options="address" placeholder="" />
    </a-form-item>
    <a-form-item label='' fieldDecoratorId="detailAddress"
      :fieldDecoratorOptions="{rules: [
           {required: true, message: '' }]
        }">
      <a-input />
    </a-form-item>
    <a-form-item label='' fieldDecoratorId="password"
      :fieldDecoratorOptions="{rules: [{ required: true, message: '' }]}">
      <a-input type="password" v-model="password" />
    </a-form-item>
    <a-form-item label=''  fieldDecoratorId="password2"
      :fieldDecoratorOptions="{rules: [{ required: true, message: ''},{validator: this.checkPassword}]}">
      <a-input type="password" />
    </a-form-item>

    <a-form-item>
      <a-button type='primary' htmlType='submit' class="loginButton">
        
      </a-button>
    </a-form-item>
  </a-form>
</template>

<script>
  import addressdata from '../../db/generateData.js'
  const {
    address
  } = addressdata
  export default {
    name: "Register",
    data() {
      return {
        address,
        password: undefined,
        formLayout: "horizontal"
      };
    },
    methods: {
      handleSubmit(e) {
        e.preventDefault();
        this.form.validateFields((err, values) => {
           let addr = '';
                        for (let i = 0; i < values.address.length; i++) {
                            addr += values.address[i];
                        }
                        addr += values.detailAddress;
          if (!err) {
            this.$axios({
              method: 'post',
              url: '/user/register',
              params: {
                userName: values.name,
                password: values.password,
                phone:values.phone,
                email:values.email,
                address:addr,
                userType:values.userType
              }
            }).then(res => {
              let result = res.data;
              if (result.status == 200) {
                this.$notification.open({
                  message: "",
                  description: ''
                });    
                setTimeout(() => {
                  this.$router.push('/login');
                }, 1000);
              } else {
                this.$notification.open({
                  message: "",
                  description: result.msg
                });
              }
            }).catch(err => {
              console.log(err);
              this.$notification.open({
                message: "",
                description: ","
              });
            })
          }

        });
      },
      checkPassword(rule, value, callback) {
        if (typeof (value) == undefined) {
          callback('');
        } else {
          if (value === this.password) {
            callback();
          } else {
            callback('');
          }
        }
      },
      checkEmail(rule, value, callback) {
        var re = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/;
        if (typeof (value) == undefined) {
          callback('');
        }
        if (re.test(value)) {
          callback();
        } else {
          callback('');
        }
      }
    },
    mounted() {

    }
  };
</script>
<style scoped>
  .login-container {
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 550px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }

  .title {
    margin: 0px auto 20px auto;
    text-align: center;
    color: #505458;
  }

  .loginButton {
    width: 480px;
    /* margin: 0px auto 20px auto; */
  }
</style>